import { _decorator, Component, instantiate, Prefab, AudioSource, assert, AudioClip, log, assetManager } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Enemy')
export class Enemy extends Component {
    @property({ type: Prefab })
    enemybullerPre: Prefab | null = null;

    @property({ type: Prefab })
    enemydiePre: Prefab | null = null;

    @property
    speed: number = 100;

    @property(AudioSource)
    _audioSource: AudioSource = null!;

    private static _audioSource?: AudioSource;
    private static _cachedAudioClipMap: Record<string, AudioClip> = {};

    // init AudioManager in GameRoot component.
    public static init(audioSource: AudioSource) {
        log('Init AudioManager !');
        Enemy._audioSource = audioSource;
    }

    public static playMusic() {
        const audioSource = Enemy._audioSource!;
        assert(audioSource, 'AudioManager not inited!');

        audioSource.play();
    }

    public static playSound(name: string) {
        const audioSource = Enemy._audioSource!;
        assert(audioSource, 'AudioManager not inited!');

        const path = `audio/sound/${name}`;
        let cachedAudioClip = Enemy._cachedAudioClipMap[path];
        if (cachedAudioClip) {
            audioSource.playOneShot(cachedAudioClip, 1);
        } else {
            assetManager.resources?.load(path, AudioClip, (err, clip) => {
                if (err) {
                    console.warn(err);
                    return;
                }

                Enemy._cachedAudioClipMap[path] = clip;
                audioSource.playOneShot(clip, 1);
            });
        }
    }

    start() {
        this.enemybullet();
    }

    update(deltaTime: number) {
        let bulletY = this.node.position.y;
        bulletY -= this.speed * deltaTime;
        this.node.setPosition(this.node.position.x, bulletY, 0);
        if (bulletY < -300) {
            this.die();
        }
    }

    //给敌机生成子弹
    enemybullet() {
        this.schedule(() => {
            let buller = instantiate(this.enemybullerPre);
            buller.setParent(this.node.parent);
            let position = this.node.position;
            buller.setPosition(position.x, position.y - 30, 0);
        }, 0.5)
    }

    die() {
        // resources.load("img/enemydie.png", SpriteFrame, (err, res) => {
        //     if (err) {
        //         console.log(err);
        //     }
        //     this.node.getComponent(Sprite).spriteFrame = res;
        // })
        let die = instantiate(this.enemydiePre);
        die.setParent(this.node.parent);
        die.setPosition(this.node.position);

        this.node.destroy();
    }

}